.authContainer {
  display:flex;
  flex-direction: column;
  height:100vh;
  justify-content:center;
  align-items:center;
  .authBtns {
    display:flex; 
    justify-content:space-between;
    width:100%;
    max-width:320px;
    .authBtn {
      cursor:pointer;
      width:150px;
      border-radius:20px;
      border:none;
      padding:10px 0px;
      background:#fff;
      font-size:12px;
      text-align:center;
    }
  }
}